.backdrop {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
  
    display: flex;
    align-items: center;
    justify-content: center;
  
    background-color: rgba(236, 111, 111, 0.2);
    transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 300ms cubic-bezier(0.4, 0, 0.2, 1);
  
    overflow-y: scroll;
    z-index: 1;
  }
  .backdrop.is-hidden {
    visibility: none;
    opacity: 0;
    pointer-events: none;
  }
  .modal {
    position: relative;
    width: 320px;
    min-height: 590px;
    border-radius: 10px;
    
  
    background-color:#f0d1a5;;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    @media screen and (min-width: 767px) {
      max-width: 410px;
      width: 100%;
      max-height: 555px;
      height: 90%;
    }
  }
  .modal-form {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 40px;
    
    &__title {
      color: #0693f2;
      font-family: Titan One;
      font-weight: 400;
      line-height: 1.13;
      text-align: center;
      
      margin-bottom: 12px;
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 1.5;
      letter-spacing: .03em;
      margin-right: 5%;
    }
  }
  
  
  .modal-close {
    display: flex;
    position: absolute;
    top: 4%;
    left: 89%;
    border-radius: 100%;
    border: 1px solid #0693f2;
  }
  
  .modal-button {
    display: block;
      margin: 0 auto;
      max-width: 200px;
      width: 75%;
      max-height: 50px;
      height: 15%;
      color: #ffffff;
      background-color: #0693f2;
  
      &:hover,
      &:focus {
     background-color:  #c2e297;
    }
  }
  .modal-label {
    display: block;
    position: relative;
    margin-bottom: 10px;
  
    font-family: Roboto;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.01em;
  
    color:  #d41443;
    
    &:last-child {
      margin-bottom: 0;
    }
  
    &__input {
      display: block;
      width: 100%;
      height: 40px;
      margin-top: 4px;
      border-radius: 4px;
      padding-left: 40px;
      padding-right: 20px;
    
      cursor: pointer;
      outline: none;
      box-sizing: border-box;
      border: 1px solid #0693f2;
      transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
  
    
    &__text {
      display: block;
      outline: none;
      resize: none;
    
      width: 100%;
      height: 120px;
      padding: 10px;
      margin-top: 4px;
      border-radius: 4px;
    
      cursor: pointer;
      box-sizing: border-box;
      border: 1px solid #0693f2;
      transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
     &__input:hover,
     &__text:hover,
     &__text:focus {
     border-color: $accent-mint-color #c2e297;
    }
  }
  
  .modal-check {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  
    font-size: 10px;
    line-height: 1.17;
    letter-spacing: 0.03em;
    margin-top: 20px;
    margin-bottom: 30px;
  
    color: #d41443;
   
  
    & span {
        width: 15px;
        height: 15px;
        border: 2px solid #0693f2;
        border-radius: 3px;
        margin-right: 7px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
  
    &__input:checked + span {
        background-color:   #d41443;
        border: none;
    }
    &__terms {
        margin-left: 4px;
        text-decoration-line: underline;
        color: var(--accent-color);
    }
  
    
  
    @media screen and (min-width: 1200px) {
        font-size: 10px;
        line-height: 1.71;
      }
  }